import React from "react";
import {
  SidebarSiteSwitcher,
  SitesList,
} from "../../components/sidebar-site-switcher/sidebar-site-switcher";
import {
  BoardsList,
  SidebarBoardsNav,
} from "../../components/sideboard-boards-nav/sidebar-boards-nav";

import { ScrollArea, Sidebar } from "@shared/ui";
import { DashboardTopbar } from "../../components/dashboard-topbar/dashboard-topbar";
import { useGlobalStore } from "../../stores/globalStore";
import { Session } from "../../types";

import { AnimatePresence, motion } from "framer-motion";

const AnimatedScrollArea = motion(ScrollArea);
interface DashboardLayout {
  children: React.ReactNode;
  sites: SitesList;
  boards: BoardsList;
  siteId: string;
  session: Session;
  onSignout: () => void;
}

const DashboardLayout: React.FC<DashboardLayout> = ({
  children,
  sites,
  boards,
  siteId,
  session,
  onSignout,
}) => {
  const { sidebarOpen } = useGlobalStore();

  return (
    <div className="flex h-[calc(100vh-50px)] overflow-y-hidden">
      <AnimatePresence>
        {sidebarOpen && (
          <AnimatedScrollArea
            key={sidebarOpen ? "open" : "closed"}
            initial={{ x: -200 }}
            animate={{ x: "0" }}
            exit={{ x: -200 }}
            transition={{ duration: 0.1 }}
            className="fixed z-10 bg-blue-50 w-max shrink-0  border-r lg:sticky lg:block "
          >
            <Sidebar>
              <SidebarSiteSwitcher siteId={siteId} sites={sites} />
              <SidebarBoardsNav siteId={siteId} boards={boards} />
            </Sidebar>
          </AnimatedScrollArea>
        )}
      </AnimatePresence>
      <main className="flex flex-1 w-full flex-col ">
        <ScrollArea className="w-full h-full">
          <DashboardTopbar session={session} onSignout={onSignout} />
          {children}
        </ScrollArea>
      </main>
    </div>
  );
};

export { DashboardLayout };
